(function() {
  'use strict';

  angular
    .module('englishGalore')
    .controller('UnitController', UnitController);

  /** @ngInject */
  function UnitController(models, units, $stateParams, resPipe) {
    var vm = this;
    vm.models = models;

     units
      .show($stateParams.id)
      .then(function(res){
        if(res.unit.section_a){
          units.getSectionA(res.unit.level_id)
        }
        if(res.unit.section_b){
          units.getSectionB(res.unit.level_id)
        }
        if(res.unit.section_c){
          units.getSectionC(res.unit.level_id)
        }
        if(res.unit.section_d){
          units.getSectionD(res.unit.level_id)
        }
        if(res.unit.section_e){
          units.getSectionE(res.unit.level_id)
        }
      });
  }
})();

