(function() {
  'use strict';

  angular
    .module('englishGalore')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController(users, $state, galoreApi, errorManager) {
    var vm = this;

    vm.user = vm.user || {};

    // Login with username and password
    vm.login = function () {
      galoreApi
        .login(vm.user)
        .then(function () {
          $state.go('main.admin.dashboard');
        })
        .catch(errorManager);
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('englishGalore')
    .controller('CreateUnitController', CreateUnitController)
    .filter('range', RangeFilter);

  /** @ngInject */
  function CreateUnitController(models,units) {
    var vm = this;
    vm.models = models;
    vm.newUnit = {
      "level_id": 1,
      "title": "Titolo",
      "unit_code": 1,
      "sub_unit_code": 3
    };

    function addUnit(){
      units
        .show({unit:vm.newUnit})
    }

    //addUnit();
  }

  function RangeFilter(){
    return function(input, min, max) {
        min = parseInt(min);
        max = parseInt(max);
        for (var i=min; i<max; i++)
          input.push(i);
        return input;
      };
  }
})();

