'use strict';

angular.module('englishGalore')
  /**
   * Response management pipeline.
   * Utilities to process a response from the moment it arrives to the models update.
   * To use in a promise chain as a pipeline
   */
  .factory('resPipe', function (longTermMemory, models, utils, $log, apiUrl) {
    var resPipe = {

      /**
       * Parse the initial response
       */
      parseResponse: function(res) {
        // Look for a valid model inside the response
        for (var i = 0; i < helpers.validModels.length; i++) {
          if(res[helpers.validModels[i]]){
            res._model = helpers.validModels[i]; // Put the name of the model in res._model
            break;
          }
        }
        return res;
      },

      /**
       * Inject some default values inside the response
       */
      injectDefaults: function(res) {
        // Parse the model depending of its nature
        var model = helpers.fetchModel(res);
        if(model){
          switch(res._model){
            case 'projects':
            case 'project':
              helpers.forEachModel(model, helpers.injectProjectDefaults);
              break;
            case 'users':
            case 'user':
              helpers.forEachModel(model, helpers.injectUserDefaults);
              break;
            default:
              break;
          }
        }
        return res;
      },

      /**
      * Mark the model as to be reloaded. This allows the resPipe to delete it
      * from cache (if present) and to add it again
      */
      markReload: function (res) {
        var model = helpers.fetchModel(res);
        helpers.forEachModel(model, function (m) {
          m._reload = true;
        });
        return res;
      },


      /**
      * Mark the model as destroyed. This allows the resPipe to delete it
      * from cache (if present)
      */
      markDestroyed: function (res) {
        var model = helpers.fetchModel(res);
        helpers.forEachModel(model, function (m) {
          m._destroyed = true;
        });
        return res;
      },

      /**
       * Sort frames and comments in projects
       */
      sortFramesAndComments: function(res) {
        var model = helpers.fetchModel(res);
        if(angular.isArray(model)){
          angular.forEach(model, function(project) {
            helpers.sortFramesAndCommentsSingle(project);
          });
        }
        else if(model){
          helpers.sortFramesAndCommentsSingle(model);
        }
        return res;
      },

      /**
       * Sort frames in cache
       */
      sortFramesCache: function(res) {
        var project = helpers.getRelatedProject(res);
        if(project && project.frames){
          utils.sortByOrder(project.frames);
        }
        return res;
      },

      /**
       * Sort Comments given order
       */
      sortCommentsCache: function(res) {
        var frame = helpers.getRelatedFrame(res);
        if(frame && frame.comments){
          utils.sortByOrder(frame.comments);
        }
        return res;
      },

      /**
       * Update models cache
       */
      updateCache: function(res) {
        var model = helpers.fetchModel(res);
        var project = null;
        var frame = null;
        var level = null;
        var unit = null;
        if(model){
          switch(res._model){
            case 'levels':
              res[res._model] = helpers.updateModelCache(models.levels, model);
              break;
            case 'unit':
              level = helpers.getRelatedLevel(res);
              res[res._model] = helpers.updateModelCache(level.units, model);
              break;
            case 'unit_section_a':
              res[res._model] = helpers.updateModelCache(models.current_unit_section_a, model);
              break;
            case 'unit_section_b':
              res[res._model] = helpers.updateModelCache(models.current_unit_section_b, model);
              break;
            case 'unit_section_c':
              res[res._model] = helpers.updateModelCache(models.current_unit_section_c, model);
              break;
            case 'unit_section_d':
              res[res._model] = helpers.updateModelCache(models.current_unit_section_d, model);
              break;
            case 'unit_section_e':
              res[res._model] = helpers.updateModelCache(models.current_unit_section_e, model);
              break;
            case 'user':
            case 'users':
              res[res._model] = helpers.updateModelCache(models.users, model);
              break;
            default:
              break;
          }
        }
        return res;
      },

      /**
       * Update models cache
       */
      cleanCache: function(res) {
        var model = helpers.fetchModel(res);
        if(model){
          switch(res._model){
            case 'users':
              models.users = [];
              if(models.current_user){
                models.users.push(models.current_user);
              }
              break;
            default:
              break;
          }
        }
        return res;
      },

      /**
       * Prune the cache, removing old projects which probably has been deleted
       */
      pruneProjectCache: function(res) {
        if(angular.isArray(res.projects)){
          var ids = [];
          var newCache = [];
          // Remember projects returned by server in the response
          angular.forEach(res.projects, function(project) {
            ids.push(project.id);
          });
          // Keep in cache only allowed projects
          angular.forEach(models.projects, function(project) {
            // If the project was present in response, or if the project is not mine, keep.
            // (Don't keep the project if it's mine but it's not present in response. This means it's been recently deleted)
            if(ids.indexOf(project.id) >= 0 || project.owner.id !== models.current_user.id){
              newCache.push(project);
            }
          });
          models.projects = newCache; // Replace the cache
        }
        return res;
      },

      /**
       * Prune the cache, removing old projects which probably has been deleted
       */
      forcePruneProjectCache: function(res) {
        if(angular.isArray(res.projects)){
          var ids = [];
          var newCache = [];
          // Remember projects returned by server in the response
          angular.forEach(res.projects, function(project) {
            ids.push(project.id);
          });
          // Keep in cache only allowed projects
          angular.forEach(models.projects, function(project) {
            // Keep only the projects that are in response
            if(ids.indexOf(project.id) >= 0){
              newCache.push(project);
            }
          });
          models.projects = newCache; // Replace the cache
        }
        return res;
      },

      /**
       * Removes a model from cache
       */
      removeFromCache: function(res) {
        var model = helpers.fetchModel(res);
        if(model){
          switch(res._model){
            case 'project_files':
            case 'project_file':
              helpers.forEachModel(model, helpers.removeProjectFileFromCache);
              break;
            default:
              break;
          }
        }
        return res;
      },

      /**
       * Set the project(s) as mine mocking an owner
       */
      setProjectAsMine: function(res) {
        var model = helpers.fetchModel(res);
        helpers.forEachModel(model, helpers.setProjectAsMineSingle);
        return res;
      },

      setAsCurrent: function(res){
        var model = helpers.fetchModel(res);
        models['current_' + res._model] = res;
        return res;
      },

      /**
       * Convert values to a human readable format
       */
      humanize: function(res) {
        var model = helpers.fetchModel(res);
        if(model){
          switch(res._model){
            case 'projects':
            case 'project':
              helpers.forEachModel(model, helpers.humanizeProject);
              break;
            default:
              break;
          }
        }
        return res;
      },

      /**
       * Store models cache in long term memory
       */
      memorizeCache: function(res) {
        angular.forEach(models, function(v, k){
          longTermMemory.memorize(k,v);
        })
        return res;
      },

      /**
       * Manage side effects, if any
       */
      sideEffects: function(res) {
        if(res.side_effects){
          angular.forEach(res.side_effects, function(v,k) {
            var project = null;
            var frame = null;

            switch(k){
              case 'projects':
              case 'project':
                helpers.updateModelCache(models.projects, v);
                break;
              case 'user':
              case 'users':
                helpers.updateModelCache(models.users, v);
                break;
              case 'frames':
              case 'frame':
                project = helpers.getRelatedProject(res);
                helpers.updateModelCache(project.frames, v);
                break;
              case 'project_files':
              case 'project_file':
                project = helpers.getRelatedProject(res);
                helpers.updateModelCache(project.project_files, v);
                break;
              case 'comments':
              case 'comment':
                frame = helpers.getRelatedFrame(res);
                helpers.updateModelCache(frame.comments, v);
                break;
              default:
                $log.warn('Don\'t know how to handle side effect "' + k + '":');
                $log.warn(v);
                break;
            }
          });
        }
        return res;
      },

      log: function (res) {
        $log.info(res);
        return res;
      },

      debugger: function (res) {
        debugger; // eslint-disable-line no-debugger
        return res;
      }
    };

    var helpers = {

      /**
       * Valid models names
       */
      validModels: [
        'levels',
        'unit',
        'unit_section_a',
        'unit_section_b',
        'unit_section_c',
        'unit_section_d',
        'unit_section_e',
        'users',
        'user'
      ],

      /**
       * Fetch the model from response
       */
      fetchModel: function(res) {
        if(res._model && res[res._model]){
          return res[res._model];
        }
        return null;
      },

      /**
      * Get the related project referenced in res
      */
      getRelatedProject: function (res) {
        return utils.findFirst(models.projects, res._project_id);
      },

      /**
      * Get the related project referenced in res
      */
      getRelatedLevel: function (res) {
        return utils.findFirst(models.levels, res[res._model].level_id);
      },

      /**
      * Get the related frame referenced in res
      */
      getRelatedFrame: function (res) {
        var project = helpers.getRelatedProject(res);
        return utils.findFirst(project.frames, res._frame_id);
      },

      /**
       * Executes a function for each model
       */
      forEachModel: function(model, func) {
        if(angular.isArray(model)){
          angular.forEach(model, func);
        }
        else if(model){
          func(model);
        }
      },

      /**
       * inject default values to a project
       */
      injectProjectDefaults: function(project) {
        // Set default image if not set
        if(!project.image){
          project.image = 'assets/images/default-img.png';
        }

        // Default download url
        if(!project.download_url){
          project.download_url = apiUrl + '/v1/projects/' + project.id + '/download';
        }
      },

      /**
       * inject default values to a user
       */
      injectUserDefaults: function(user) {
        return user;
      },

      /**
       * Set a project as mine
       */
      setProjectAsMineSingle: function(project) {
        if(!project.owner && models.current_user && models.current_user.id){
          project.owner = { id: models.current_user.id };
        }
      },

      /**
       * Sort the frames and comments of a project by order
       */
      sortFramesAndCommentsSingle: function(project) {
        utils.sortByOrder(project.frames);
        angular.forEach(project.frames, function(frame) {
          utils.sortByOrder(frame.comments);
        });
      },

      humanizeProject: function(project) {
        // force private to be a boolean (defaults to false), and set private string
        project.private = !!project.private;
        if(project.private){
          project.privateString = 'Private Project';
        }
        else{
          project.privateString = 'Public Project';
        }
      },

      updateModelCache: function (modelCache, model) {
        var newData = model;

        // modelCache is array
        if(angular.isArray(modelCache)){

          // if model is array, recursion
          if(angular.isArray(model)){
            newData = [];
            angular.forEach(model, function(m) {
              if(m){
                var merged = helpers.updateModelCache(modelCache, m); // recursion
                if(merged){
                  newData.push(merged);
                }
              }
            });
          }

          // single model
          else if(model){
            // if model is marked as _reload, remove it from cache and add it again
            if(model._reload){
              utils.removeFirst(modelCache, model.id);
              delete model._reload; // Delete _reload metadata not to store it in cache
              newData = utils.mergeOrPush(modelCache, model);
            }
            // if model is marked as _destroyed, remove it from cache
            else if(model._destroyed){
              utils.removeFirst(modelCache, model.id);
            }
            // otherwise, just update the cache
            else{
              newData = utils.mergeOrPush(modelCache, model);
            }
          }
        }

        return newData;
      }
    };

    return resPipe;
  });

'use strict';

angular.module('englishGalore')
  /**
   * Long Term Memory
   * Persist data to make it available after the browser is closed
   */
  .factory('longTermMemory', function () {

    var longTermMemory = {
      memorize: function(key, val) {
        localStorage[key] = angular.toJson(val);
      },

      remember: function(key) {
        try {
          return angular.fromJson(localStorage[key]);
        }
        catch(e){
          delete localStorage[key];
          return null;
        }
      },

      forget: function(key) {
        delete localStorage[key];
      },

      oblivion: function() {
        localStorage.clear();
      }
    };

    return longTermMemory;
  });

(function() {
  'use strict';

  angular
    .module('englishGalore')
    .directive('galoreSidebar', galoreSidebar);

  /** @ngInject */
  function galoreSidebar($timeout) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/galoreSidebar/galoreSidebar.html',
      controller: SidebarController,
      controllerAs: 'sidebar',
      link: SidebarLink,
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function SidebarController(models, $state) {
      var vm = this;

      vm.models = models;
      vm.state = $state;
    }

    function SidebarLink(){
      $timeout(function () {
        angular.element(window).trigger('resize'); // Trigger a resize event to adjust AdminLTE height
      });
    }
  }

})();

