'use strict';

angular.module('englishGalore')
  /**
   * My Project Overview API service
   */
   .factory('galoreApi', function (
      $q,
      $http,
      $auth,
      apiUrl,
      unitApiUrl,
      showLevelsApiUrl,
      getSectionapiUrl
    ) {
      // Cache the promises (just to avoid to call the same API before the previous has finished)
      var cache = {};

      // Api Helpers (accessible only from this factory)
      var helpers = {
        /**
         * Try to retrieve the result from cache, or make HTTP call
         * @param  {String} method  The HTTP method
         * @param  {String} url The API url
         * @param  {Object} data    The body
         * @param  {Object} headers The headers
         * @param  {Boolean} keep   If true, the promise is cached even after it's been fulfilled
         * @return {Promise}        A promise of the response
         */
        cacheOrHttp: function(method, url, data, headers, keep) {
          var key = method + url + (data ? angular.toJson(data) : '');

          var config = {};
          if(method) config.method = method.toUpperCase();
          if(url) config.url = apiUrl + url;
          if(data) config.data = data;
          if(headers) config.headers = headers;

          if (!cache[key]) {
            var promise;
            var err = false;
            switch(method){
              case 'get':
              case 'GET':
              case 'post':
              case 'POST':
              case 'put':
              case 'PUT':
              case 'patch':
              case 'PATCH':
              case 'delete':
              case 'DELETE':
                promise = $http(config);
                break;
              default:
                err = true;
                promise = $q.reject();
                break;
            }
            if(!err){
              cache[key] = promise.finally(function(response,err) {
                if(!keep){
                  delete cache[key]; // delete the cache, so the next call will be fresh
                }
                if(err) { return $q.reject(err); }
                if(response) { return response; }
              });
            }
          }

          return cache[key];
        },


        /**
         * Expect a 200 response and return data.
         * Otherwise, return a rejected promise.
         */
        expect200: function (response) {
          if(response.status === 200){
            return response.data;
          }
          else{
            return $q.reject(response);
          }
        }
      };

      var galoreoApi = {
        /** USER API **/

        /**
         * Authenticate the user
         */
        login: function(data) {
          if(!cache.login){
            cache.login = $auth
              .submitLogin(data)
              .finally(function(response,err) {
                delete cache.login;
                if(err) { return $q.reject(err); }
                if(response) { return response; }
              });
          }
          return cache.login;
        },

        /**
         * Retrieve user data (together with role permissions)
         */
        getMe: function(){
          return helpers
            .cacheOrHttp('get', '/v1/me')
            .then(helpers.expect200);
        },

        /**
         * Logout the user
         */
        logout: function() {
          if(!cache.logout){
            cache.logout = $auth
              .signOut()
              .finally(function(response,err) {
                delete cache.logout;
                if(err) { return $q.reject(err); }
                if(response) { return response; }
              });
          }
          return cache.logout;
        },

        /**
        * Change the password of the user
        */
        changePassword: function (data) {
          if(!cache.changePassword){
            cache.changePassword = $auth
              .updatePassword(data)
              .finally(function (response, err) {
                  delete cache.changePassword;
                  if(err) { return $q.reject(err); }
                  if(response) { return response; }
              });
          }
          return cache.changePassword;
        },

        /**
        * Create Unit
        */
        createUnit: function(data) {
          return helpers
          .cacheOrHttp('post', unitApiUrl, data)
          .then(helpers.expect200);
        },

        /**
        * Create Unit
        */
        showUnit: function(id) {
          return helpers
          .cacheOrHttp('get', unitApiUrl + '/' + id)
          .then(helpers.expect200);
        },

        /**
        * Get Section A by id section
        */
        getSectionA: function(id){
          return helpers
          .cacheOrHttp('get', getSectionapiUrl + 'a/' + id)
          .then(helpers.expect200);
        },

        /**
        * Get Section B by id section
        */
        getSectionB: function(id){
          return helpers
          .cacheOrHttp('get', getSectionapiUrl + 'b/' + id)
          .then(helpers.expect200);
        },

        /**
        * Get Section C by id section
        */
        getSectionC: function(id){
          return helpers
          .cacheOrHttp('get', getSectionapiUrl + 'c/' + id)
          .then(helpers.expect200);
        },

        /**
        * Get Section D by id section
        */
        getSectionD: function(id){
          return helpers
          .cacheOrHttp('get', getSectionapiUrl + 'd/' + id)
          .then(helpers.expect200);
        },

        /**
        * Get Section E by id section
        */
        getSectionE: function(id){
          return helpers
          .cacheOrHttp('get', getSectionapiUrl + 'e/' + id)
          .then(helpers.expect200);
        },

        /**
         * Show all levels
         */
        indexLevels: function(name, token){
          return helpers
            .cacheOrHttp('get', showLevelsApiUrl)
            .then(helpers.expect200);
        }


        /* API */
      };

      return galoreoApi;
   });

