'use strict';

angular.module('englishGalore')
  /**
   * My Project Overview API service
   */
   .factory('can', function (models) {

      var helpers = {
        instanceCondition: function(action, resource, instance) {
          var perms = models.current_user && models.current_user.can;
          // Safely get condition function (avoiding undefined errors)
          var cond = perms && perms.instanceConditions && perms.instanceConditions[action] && perms.instanceConditions[action][resource];
          // If no condition was found, return true, else evaluate the condition
          return !cond || cond(instance);
        },
        canManage: function(resource, instance) {
          var perms = models.current_user && models.current_user.can;
          return !!perms && !!perms.manage && (perms.manage.indexOf(resource) >= 0) && helpers.instanceCondition('manage', resource, instance);
        },
        canUpdate: function(resource, instance) {
          var perms = models.current_user && models.current_user.can;
          return !!perms && !!perms.update && (perms.update.indexOf(resource) >= 0) && helpers.instanceCondition('update', resource, instance);
        },
        canShow: function(resource, instance) {
          var perms = models.current_user && models.current_user.can;
          return !!perms && !!perms.show && (perms.show.indexOf(resource) >= 0) && helpers.instanceCondition('show', resource, instance);
        },
        canIndex: function(resource, instance) {
          var perms = models.current_user && models.current_user.can;
          return !!perms && !!perms.index && (perms.index.indexOf(resource) >= 0) && helpers.instanceCondition('index', resource, instance);
        },
        customCan: function(action, resource, instance) {
          var perms = models.current_user && models.current_user.can;
          return !!perms && !!perms[action] && (perms[action].indexOf(resource) >= 0) && helpers.instanceCondition(action, resource, instance);
        }
      };

      var can = function(action, resource, instance){
        switch(action){
          case 'manage':
            return helpers.canManage(resource, instance);
          case 'update':
            return helpers.canManage(resource, instance) || helpers.canUpdate(resource, instance);
          case 'show':
            return helpers.canManage(resource, instance) || helpers.canUpdate(resource, instance) || helpers.canShow(resource, instance);
          case 'index':
            return helpers.canShow(resource, instance) || helpers.canIndex(resource, instance);
          default:
            return helpers.customCan(action, resource, instance);
        }
      };

      return can;
   });

(function() {
  'use strict';

  angular
    .module('englishGalore')
    .directive('can', canDirective);

  /** @ngInject */
  function canDirective(can, $log, models) {
    var directive = {
      restrict: 'A',
      link: linkFunc,
      scope: {
        instance: '='
      }
    };

    return directive;

    function linkFunc(scope, el, attr) {
      var watcher;
      var shown;
      var action;
      var resource;
      var negative;
      scope.models = models;

      // Update the visibility of the element
      var updateVisibility = function() {
        var toShow = can(action, resource, scope.instance);
        if(negative){ toShow = !toShow; } // invert the condition if needed
        // Show the element if not shown
        if(toShow && !shown){
          el.removeClass('can-hide');
          shown = true;
        }

        // Hide the element if shown
        else if(!toShow && shown){
          el.addClass('can-hide');
          shown = false;
        }
      };

      // Parse can attribute
      var parsed = attr.can.split(':');
      if(parsed.length !== 2){
        $log.error('Syntax error in "can" directive: ' + angular.toJson(attr.can));
        return;
      }

      // If the action starts with "!", the action is negative
      negative = parsed[0].slice(0, 1) === '!';


      // In that case, remove ! from the string
      if(negative){
        parsed[0] = parsed[0].substr(1);
      }

      // Extract the action and the resource
      action = parsed[0];
      resource = parsed[1];

      // Update the visibility for the first time
      shown = true;
      updateVisibility();

      // Update the visibility as soon as the permissions change
      watcher = scope.$watch('models.current_user.can', function(newVal, oldVal) {
        if(newVal && oldVal !== newVal){
          updateVisibility();
        }
      });

      // Update the visibility as soon as the instance changes
      watcher = scope.$watch('instance', function(newVal, oldVal) {
        if(newVal && oldVal !== newVal){
          updateVisibility();
        }
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }
  }

})();

