(function() {
  'use strict';

  angular
    .module('englishGalore')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log,
    $rootScope,
    uiState,
    defaultBodyClasses
    ) {
        // Bind the uiState to $rootScope as it's used in index.html
        $rootScope.uiState = uiState;

        // On state change
        var stateChangeSuccess = $rootScope.$on('$stateChangeSuccess', function (event, toState) {
          var pms = toState.params || {};
          uiState.bodyClasses = pms.bodyClasses || defaultBodyClasses;
        });

        // Destroy watchers
        $rootScope.$on('$destroy', function() {
          stateChangeSuccess();
        });
  }

})();

(function() {
  'use strict';

  angular
    .module('englishGalore')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {

    $stateProvider
      .state('main', {
        abstract: true,
        templateUrl: 'app/pages/main/main.html'
      })
      .state('main.admin', {
        templateUrl: 'app/pages/admin/admin.html',
        controller: 'AdminController',
        controllerAs: 'vm',
        resolve: {
          currentUser: function($auth, $q, users) {
            return $auth
              .validateUser()
              .then(function(res){
                if(res.signedIn !== true){
                  return $q.reject();
                }
              })
              .then(users.me)
              .catch(function(err){
                return null;
              });
          },
          levelsData: function(levels){
            return levels.index();
          }
        },
        onEnter: function($timeout, $state, levelsData, currentUser){
          if(!currentUser){
            $timeout(function(){
              $state.go('main.login');
            });
          }
        }
      })
      .state('main.logout', {
        url: '/logout',
        onEnter: function($q, errorManager, galoreApi, $state, toastr, models, longTermMemory) {
          galoreApi.logout()
            .finally(function() {
              delete models.current_user;
              models.users = [];
              longTermMemory.forget('user');
              longTermMemory.forget('current_user');
              $state.go('main.login');
            });
        }
      })
      .state('main.login', {
        url: '/login',
        templateUrl: 'app/pages/login/login.html',
        controller: 'LoginController',
        controllerAs: 'vm',
        params: {
          bodyClasses: 'hold-transition login-page'
        }
      })
      .state('main.admin.unit', {
        url: '/units/:id',
        templateUrl: 'app/pages/unit/unit.html',
        controller: 'UnitController',
        controllerAs: 'vm'
      })
      .state('main.admin.profile', {
        url: '/profile',
        templateUrl: 'app/pages/profile/profile.html',
        controller: 'ProfileController',
        controllerAs: 'vm'
      })
      .state('main.admin.dashboard', {
        url: '/',
        templateUrl: 'app/pages/dashboard/dashboard.html'
      })
      .state('main.admin.createUnit', {
        url: '/create-unit',
        templateUrl: 'app/pages/createUnit/createUnit.html',
        controller: 'CreateUnitController',
        controllerAs: 'vm'
      })

    $urlRouterProvider.otherwise('/');
  }

})();

